// action-types
export const SET_CALENDAR = 'SET_CALENDAR'

// state
const initialState = {
    date: null
}

// action

// reducer
const calendar = (state = initialState, action) => {
    switch (action.type) {
        case SET_CALENDAR: {
            const { date } = action.doc
            return { date }
        }
        default:
            return state
    }
}
  
export default calendar
  
// selector
export const getSelectedDate = state => state.calendar.date