// action-types
export const SET_MENU = 'SET_MENU'

// state
const initialState = {
    menu: ''
}

// action

// reducer
const menu = (state = initialState, action) => {
    switch (action.type) {
        case SET_MENU: {
            const { menu } = action.doc
            return { menu }
        }
        default:
            return state
    }
}
  
export default menu
  
// selector
export const getCurMenu = state => state.menu.menu