// action-types
export const LOADING_SHOW = 'LOADING_SHOW'

// state
const initialState = {
    show: false
}

// action

// reducer
const loading = (state = initialState, action) => {
    switch (action.type) {
        case LOADING_SHOW: {
            const { show } = action.doc
            return { show }
        }
        default:
            return state
    }
}
  
export default loading
  
// selector
export const getLoadingShow = state => state.loading.show